.custom-table{
  border: 1px solid #D7D7D7;
  border-radius: 5px 5px 0px 0px;
  width: 100%;
  border-spacing: 0;
  margin-top: 20px;
  td{
    min-width: 55px;
    overflow: hidden;
  }
  &__title{
    border-bottom: 1px solid #D7D7D7;
    td{
      padding: 15px;
      border-right: 1px solid #D7D7D7;
      &:last-child{
        border-right: none;
      }
    }
  }
  &__content{
    &:nth-child(even){
      td{
        background-color: #CFF5F1;
      }
    }
    td{
      padding: 15px;
      border-top: 1px solid #D7D7D7;
      border-right: 1px solid #D7D7D7;
      overflow: hidden;
      vertical-align: middle;
      &:last-child{
        border-right: none;
      }
    }
  }
}

.table-text{
  white-space: nowrap;
  text-overflow: ellipsis;
}