.sort{
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &-item{
    & + &{
      margin-left: 20px;
    }
  }
  &-link{
    padding: 12px 30px;
    display: block;
    color:#0C89E4;
    border: 1px solid #D7D7D7;
    border-radius: 5px;
    box-shadow: 0px 4px 4px transparent;
    transition: 0.5s box-shadow linear;
    &:hover{
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
  }
  &-block{
    display: flex;
    align-items: center;
  }
  &-select{
    select{
      height: 40px;
      width: 70px;
      border: 1px solid #D4D4D4;
      border-radius: 5px;
      outline: none;
      font-size: 14px;
      color: #737373;
    }
  }
}

.search{
  position: relative;
  &__clear,&__icon{
    position: absolute;
  }
  &__input{
    height: 40px;
    width: 325px;
    border: 1px solid #D4D4D4;
    outline: none;
    border-radius: 5px;
    padding: 12px 28px 12px 42px;
    &::placeholder{
      color: #0C89E4;
      font-size: 14px;
    }
  }
  &__clear{
    top: 50%;
    margin-top: -10px;
    right: 14px;
  }
  &__icon{
    top: 50%;
    margin-top: -9px;
    left:15px;
  }
}