.filter{
  display: grid;
  grid-template-columns: repeat(2,minmax(0,1fr));
  row-gap: 10px;
  column-gap: 10px;
  &-group{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px;
    background: #FFFFFF;
    border: 1px solid #D4D4D4;
    border-radius: 5px;
    &__input{
      width: 735px;
      background: #F9FCFE;
      border: 1px solid #D4D4D4;
      border-radius: 2px;
      padding: 9px 15px;
      color:#3D3D3D;
      font-size: 14px;
      outline: none;
    }
    &__text{
      font-size: 14px;
      color: #0C89E4;
    }
  }
}