.timetable{
  display: flex;
  &-block{
    flex: 1;
    &__wrapper{
      display: flex;
    }
    &__title{
      display: flex;
      align-items: center;
      justify-content: center;
      background: #ECF6FD;
      border-radius: 5px;
      height: 30px;
      width: calc(100% - 85px);
      margin-left: auto;
    }
  }
  &-title{
    padding: 25px;
  }
  &-item{
    flex: 100%;
    max-width: 118px;
    border-right: 1px solid #D7D7D7;
    border-top: 1px solid #D7D7D7;
    &_time{
      flex:80px;
      flex-shrink: 0;
    }
    &__block{
      display: flex;
      align-items: center;
      justify-content: center;
      height: 45px;
    }
  }
  &-empty{
    border-top: none;
  }
  &-tile{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding: 5px 15px;
    &__text{
      font-size: 12px;
      line-height: 14px;
      padding-right: 20px;
    }
  }
  &-green{
    background-color: #BED9B5;
  }
  &-orange{
    position: relative;
    background: rgba(248, 237, 142, 0.2);
    &::before{
      content: '';
      position: absolute;
      height: 100%;
      width: 8px;
      background-color: #F8ED8E;
      display: block;
      left: 0;
      top: 0;
    }
  }
  &-red{
    position: relative;
    background: rgba(250, 165, 118, 0.2);
    &::before{
      content: '';
      position: absolute;
      height: 100%;
      width: 8px;
      background-color: #FAA576;
      display: block;
      left: 0;
      top: 0;
    }
  }
  &-purple{
    position: relative;
    background: rgba(114, 96, 227, 0.2);
    &::before{
      content: '';
      position: absolute;
      height: 100%;
      width: 8px;
      background-color: #7260E3;
      display: block;
      left: 0;
      top: 0;
    }
  }
  &-lightgreen{
    position: relative;
    background: rgba(173, 255, 47, 0.2);
    &::before{
      content: '';
      position: absolute;
      height: 100%;
      width: 8px;
      background-color: #ADFF2F;
      display: block;
      left: 0;
      top: 0;
    }
  }
  &-break{
    background-color: #CFF5F1;
    svg{
      margin-right: 5px;
    }
  }
}