.aside{
  width: 75px;
  flex-shrink: 0;
  margin-right: 10px;
  &-select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    & + &{
      margin-top: 15px;
    }
    select{
      width: 285px;
      border: none;
      outline: none;
      height: 35px;
    }
  }
  &-sort{
    padding: 15px;
    border: 1px solid #D7D7D7;
    border-radius: 5px;
    min-height: 450px;
    margin-top: 15px;
    .checkbox + .checkbox{
      margin-top: 20px;
    }
  }
  &-title{
    font-size: 20px;
  }
  &-types{
    margin-top: 20px;
  }
}

.right-aside{
  width: 440px;
  flex-shrink: 0;
  margin-left: 55px;
}

.type-list{
  margin-top: 20px;
  &__item{
    display: flex;
    align-items: center;
    & + &{
      margin-top: 17px;
    }
    &::before{
      content: '';
      width: 8px;
      height: 8px;
      border-radius: 50%;
      display: block;
      margin-right: 10px;
    }
    &_yellow{
      &::before{
        background-color: #F8ED8E;
      }
    }
    &_orange{
      &::before{
        background-color: #FAA576;
      }
    }
    &_purple{
      &::before{
        background-color: #7260E3;
      }
    }
    &_green{
      &::before{
        background-color: #ADFF2F;
      }
    }
  }
}