.popup{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(13, 84, 135, 0.2);
  padding: 30px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  &-wrapper{
    width: 60%;
    background-color: #fff;
    padding: 15px 25px;
    border-radius: 10px;
    max-height: 100vh;
    overflow-y: scroll;
  }
  &-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &-title{
    font-size: 20px;
    color:#3D3D3D;
  }
  &-controls{
    display: flex;
    align-items: center;
  }
  &-control + &-control{
    margin-left: 25px;
  }
  &-btns{
    margin-top: 20px;
    display: flex;
    align-items: center;
  }
  &__btn{
    padding: 12px 30px;
    color:#fff;
    font-size: 14px;
    background: #0C89E4;
    border-radius: 5px;
    &_disable{
      background: #D4D4D4;
      color:#737373;
    }
    & + &{
      margin-left: 20px;
    }
  }
  &-content{
    margin-top: 20px;
  }
  &-patient{
    display: flex;
    align-items: center;
    font-size: 16px;
    color: #0C89E4;
    svg{
      margin-right: 10px;
    }
  }
}

.patient{
  &-info{
    display: flex;
    align-items: center;
    & + &{
      margin-top: 25px;
    }
    &__wrapper{
      margin-top: 30px;
    }
    &__block{
      display: flex;
      align-items: center;
      & + &{
        margin-left: 35px;
      }
    }
    &__text{
      margin-right: 15px;
    }
    &__desc{
      display: flex;
      align-items: center;
      svg{
        margin-right: 5px;
      }
    }
    &__year{
      display: flex;
      align-items: center;
      justify-content: center;
      background: #FAFAFA;
      border: 1px solid #D7D7D7;
      width: 50px;
      height: 35px;
      border-radius: 5px;
    }
    &__select{
      background: #FAFAFA;
      border-radius: 5px;
      height: 35px;
      display: flex;
      align-items: center;
      padding: 0 10px;
      select{
        border:none;
        outline: none;
        height: 35px;
        background: #FAFAFA;
      }
      svg{
        margin-left: 15px;
      }
    }
    &_textarea{
      flex-direction: column;
      align-items: flex-start;
    }
    &__textarea{
      resize: none;
      margin-top: 10px;
      background: #FAFAFA;
      border: 1px solid #D7D7D7;
      border-radius: 5px;
      width: 100%;
      height: 70px;
    }
  }
}

.popup{
  &-table{
    width: 100%;
    margin-top: 10px;
    border-spacing: 0;
    td{
      overflow: hidden;
      background-color: #FAFAFA;
      padding: 10px;
      border-top: 1px solid #D7D7D7;
      border-left: 1px solid #D7D7D7;
      &:last-child{
        border-right: 1px solid #D7D7D7;
      }
    }
    &__tr-title{
      td{
        background-color: transparent;

      }
    }
    &__text{
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }
}

.gynec-type{
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  &__select{
    width: 245px;
    height: 35px;
    background: #FAFAFA;
    border-radius: 5px;
    margin-left: 15px;
    select{
      width: 100%;
      height: 100%;
      background-color: transparent;
      border:none;
    }
  }
}