body{
  font-family: 'Lato', sans-serif;
  font-size: 14px;
}

.page-wrapper{
  display: flex;
  padding: 18px 14px 18px 0;
}

.content{
  width: 100%;
}

.checkbox{
  display: flex;
  align-items: center;
  &__input{
    display: none;
    &:checked ~ .checkbox__checkmark{
      background-color: #fff;
      border-color:#0C89E4;
      &::before{
        opacity: 1;
      }
    }
  }
  &__checkmark{
    width: 20px;
    height: 20px;
    display: block;
    background: #E1E1E1;
    border-radius: 2px;
    border: 1px solid transparent;
    transition: 0.5s all linear;
    position: relative;
    &::before{
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -5px;
      margin-left: -5px;
      opacity: 0;
      width: 10px;
      height: 10px;
      display: block;
      background: url("../img/check.svg") center center no-repeat;
      background-size: contain;
      transition: 0.5s all linear;
    }
  }
  &__text{
    margin-left: 13px;
  }
}