.popup{
  &-tabs{
    margin-top: 60px;
    &__nav{
      display: flex;
      align-items: center;
    }
    .tabs{
      &-nav{
        &__item{
          display: flex;
          align-items: center;
          justify-content: center;
          height: 40px;
          width: 145px;
          color: #737373;
          background: #FAFAFA;
          box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.15);
          border-radius: 5px 5px 0px 0px;
          margin-right: 20px;
          &.active{
            background: #63BE09;
            color:#fff;
          }
        }
      }
      &-content{
        background: #FFFFFF;
        border: 1px solid #D7D7D7;
        border-radius: 0px 5px 5px 5px;
        &__item{
          padding: 25px 25px 45px 25px;
        }
      }
    }
  }
}

.tabs{
  &-content{
    &__item{
      display: none;
      &.active{
        display: block;
      }
    }
  }
  &-input{
    display: flex;
    align-items: center;
    &_textarea{
      flex-direction: column;
      align-items: flex-start;
      .tabs-input__title{
        margin-bottom: 10px;
      }
    }
    .checkbox{
      background: #FAFAFA;
      border-radius: 5px;
      padding: 10px 15px;
      margin-left: 10px;
    }
    & + &{
      margin-top: 25px;
    }
    &__title{
      margin-bottom: 0px;
    }
    &__block{
      display: flex;
      align-items: center;
      .tabs-input{
        margin-top: 0;
      }
      .tabs-input + .tabs-input{
        margin-left: 40px;
      }
    }
    &__item{
      background: #FAFAFA;
      border: 1px solid #D7D7D7;
      border-radius: 5px;
      height: 35px;
      max-width: 70px;
      margin-left: 15px;
      padding: 0 15px;
      &_big{
        max-width: 110px;
      }
    }
    &__block + &__block{
      margin-top: 30px;
    }
    &__textarea{
      resize: none;
      width: 100%;
      height: 130px;
      background: #FAFAFA;
      border: 1px solid #D7D7D7;
      border-radius: 5px;
      padding: 15px;
    }
  }
}